h1 {
	font-weight: bold;
	margin: 0;
}

h2 {
	text-align: center;
}

p {
	font-size: 20px;
	font-weight: 140;
	line-height: 20px;
	letter-spacing: 0.5px;
    margin: 20px 0 30px;
    margin-bottom: 50px;
}

span {
	font-size: 17px;
}

a {
	color: #333;
	font-size: 14px;
	text-decoration: none;
	margin: 15px 0;
}

 .login__signIn , .login__signUp ,.login__ghostsignUp ,.login__ghostsignIn {
	border-radius: 50px;
	border: 1px solid #FF4B2B;
    background-color: #FF4B2B;    
	color: #FFFFFF;
	font-size: 12px;
	font-weight: bold;
	padding: 12px 45px;
	letter-spacing: 1px;
	text-transform: uppercase;
    transition: transform 80ms ease-in;
    margin-top: 10px;
}

.login__ghostsignIn , .login__ghostsignUp{
    border: 1px solid black;    
    border-radius: 50px;
    margin-top: 10px;
}

.login__ghostsignIn:active , .login__ghostsignUp:active {
	transform: scale(0.95);
}

.login__signIn:active , .login__signUp:active {
    transform: scale(0.95);
}

.login__signIn:focus ,.login__signUp:focus{
	outline: none;
}

.login__ghostsignIn:focus ,.login__ghostsignUp:focus {
	outline: none;
}

button.ghost {
    background-color: transparent;     
	border-color: #FFFFFF;
}

.form_Signin  ,.form_Signup{
	background-color: #FFFFFF;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	padding: 0 50px;
	height: 100%;
	text-align: center;
}

.login__email , .login__password , .register__fnname , .register__email , .register__password ,.register__Cpassword ,.register__lnname {
	background-color: #eee;
	border: none;
	padding: 12px 15px;
    margin: 8px 0;
    margin-bottom: 10px;
    width: 100%;
    border-radius: 4px;
}

.container {
	background-color: #fff;
	border-radius: 10px;
  	box-shadow: 0 20px 28px rgba(0,0,0,0.25), 
			0 10px 10px rgba(0,0,0,0.22);
	position: relative;
	overflow: hidden;
	width: 80%;
	max-width: 100%;
    min-height: 480px;
    margin-top: 40px;
 
    
}

.form-container {
	position: absolute;
	top: 0;
	height: 100%;
	transition: all 0.6s ease-in-out;
}

.sign-in-container {
	left: 0;
	width: 50%;
	z-index: 2;
}

.container.right-panel-active .sign-in-container {
	transform: translateX(100%);
}

.sign-up-container {
	left: 0;
	width: 50%;
	opacity: 0;
	z-index: 1;
}

.container.right-panel-active .sign-up-container {
	transform: translateX(100%);
	opacity: 1;
	z-index: 5;
	animation: show 0.6s;
}

@keyframes show {
	0%, 49.99% {
		opacity: 0;
		z-index: 1;
	}
	
	50%, 100% {
		opacity: 1;
		z-index: 5;
	}
}

.overlay-container {
	position: absolute;
	top: 0;
	left: 50%;
	width: 50%;
	height: 100%;
	overflow: hidden;
	transition: transform 0.6s ease-in-out;
	z-index: 100;
}

.container.right-panel-active .overlay-container{
	transform: translateX(-100%);
}

.overlay {
	background: #FF416C;
	background: -webkit-linear-gradient(to right, #FF4B2B, #FF416C);
	background: linear-gradient(to right, #FF4B2B, #FF416C);
	background-repeat: no-repeat;
	background-size: cover;
	background-position: 0 0;
	color: black;
	position: relative;
	left: -100%;
	height: 100%;
	width: 200%;
  	transform: translateX(0);
    transition: transform 0.6s ease-in-out;
    
}

.container.right-panel-active .overlay {
  	transform: translateX(50%);
}

.overlay-panel {
	position: absolute;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	padding: 0 40px;
	text-align: center;
	top: 0;
	height: 100%;
	width: 50%;
	transform: translateX(0);
    transition: transform 0.6s ease-in-out; 
}

.overlay-left {
	transform: translateX(-20%);
}

.container.right-panel-active .overlay-left {
	transform: translateX(0);
}

.overlay-right {
	right: 0;
	transform: translateX(0);
}

.container.right-panel-active .overlay-right {
	transform: translateX(20%);
}

.social-container {
	margin: 20px 0;
}

.social-container a {
	border: 1px solid #DDDDDD;
	border-radius: 50%;
	display: inline-flex;
	justify-content: center;
	align-items: center;
	margin: 0 5px;
	height: 40px;
	width: 40px;
}
 